










































export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: false,
			duration: 500,
			baise:'rgba(255, 255, 255, .3)',
			lanse: '#918984'
		}
	},
	methods: {
		launchFlag(){
			/**
			 * 向本地存储中设置launchFlag的值，即启动标识；
			 */
			uni.setStorage({
					key: 'launchFlag',
					data: true,
			});
			uni.navigateTo({
				url: "/pages/index/index/"
			})
		}
	}
}
